import React from 'react';
import AxiosInstance from '../../AxiosInstance';
import { DataGrid } from '@material-ui/data-grid';

const columns = [
    { field: 'salad', headerName: 'Salad', width: 100 },
    { field: 'bacon', headerName: 'Bacon', width: 100 },
    { field: 'cheese', headerName: 'Cheese', width: 100 },
    { field: 'meat', headerName: 'Meat', width: 100 },
    { field: 'price', headerName: 'Price', width: 100 },
  ];

  const rows = [];

class OrderHistory extends React.Component {
    state = {
        gridRows : []
    }
    

    componentDidMount() {
        AxiosInstance.get('/orders.json').then(res => {
            for (let i in res.data) {
                let order = res.data[i].ingrediants;
                order.price = res.data[i].price;
                order.id = i;
                rows.push({ ...order })
            }
            this.setState({
                gridRows: rows
            })
            console.log(rows);
        }).catch(err => {
            console.log('error --- ', err)
        })
    }

    render() {
        return(
            <div className='container'>
                <h2>Order History</h2>
                <div style={{ height: 500, width: '100%' }}>
                    <DataGrid rows={this.state.gridRows} columns={columns} />
                </div>
            </div>
            
        );
    }
}

export default OrderHistory;