import React from 'react';
import CheckoutSummary from '../../components/checkoutSummary/CheckoutSummary';
import { connect } from 'react-redux';
import Spinner from '../../components/ui/spinner/Spinner';
import AxiosInstance from '../../AxiosInstance';

class Checkout extends React.Component {

    state = {
        loading: false,
    }
    
    cancelCheckoutHandler = () => {
        this.props.history.goBack();
    }

    continueCheckoutHandler = () => {
        // this.props.history.push('/checkout/condact-data')
        const order = {
            ingrediants: this.props.ings,
            price: this.props.price
        }
        this.setState({
            loading: true
        })
        AxiosInstance.post('/orders.json', order)
            .then((res) => {
                this.setState({
                    loading: false
                })
                const headers = {
                    "Content-Type": "application/json",
                    "Authorization": "key=" + this.props.storeKey
                }
                const data = {
                    'notification': {
                        'title': 'Order Success',
                        'body': 'Your Order Successfully Placed!!!',
                        'click_action': window.location.origin+'/',
                        // 'icon': window.location.origin + '/icon.png'
                    },
                    'to': this.props.firebaseToken
                }
                AxiosInstance.post('https://fcm.googleapis.com/fcm/send', JSON.stringify(data), {headers: headers}).then(res => {
                    console.log('Notification sent', res)
                }).catch(err => {
                    console.log('err', err)
                })
                this.props.history.push('/');
            }).catch((error) => {
                this.setState({
                    loading: false
                })
                console.log('Error ->', error)
                alert('Error saving order')
            })
    }
    render() {
        // console.log(window.location.origin)
        let checkOutSummary = <CheckoutSummary
            ingrediants={this.props.ings}
            cancelCheckout={this.cancelCheckoutHandler}
            continueCheckout={this.continueCheckoutHandler}
        />
        if(this.state.loading) {
            checkOutSummary = <Spinner />
        }
        return (
            <div>
                {checkOutSummary}
                {/* <Route path={this.props.match.path + '/condact-data'} 
                component={CondactData} /> */}
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        ings: state.ingrediants,
        price: state.totalPrice,
        firebaseToken: state.firebaseToken,
        storeKey: state.storeKey
    }
}
export default connect(mapStateToProps)(Checkout);