import React from 'react';
import Layout from './components/layout/Layout';
import BurgerBuilder from './containers/burgerBuilder/BurgerBuilder';
import Checkout from './containers/checkout/Checkout';
import { StyleRoot } from 'radium';
import { Route, Switch } from 'react-router-dom';

import { initializeFirebase } from './push-notification';
import * as actionType from '../src/store/Actions';
import { connect } from 'react-redux';
import OrderHistory from './containers/history/orderHistory';

class App extends React.Component {

  componentDidMount() {
    initializeFirebase().then(res => {
      console.log(res)
      this.props.setFirebaseToken(res);
    })
    
  }

  render() {
    // console.log('window.PUBLIC_URL', window.location.href)
    return (
      <StyleRoot>
        <Layout>
          <Switch>
            <Route path='/checkout' component={Checkout} />
            <Route path='/order-history' component={OrderHistory} />
            <Route path='/' exact component={BurgerBuilder} />
          </Switch>
        </Layout>
      </StyleRoot>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setFirebaseToken: (token) => dispatch({type: actionType.SET_FIREBASE_TOKEN, firebaseToken: token})
  }
}

export default connect(null, mapDispatchToProps)(App);
