import React from 'react';


const Button = (props) => {
    let ButtonStyles = {
        Button: {
            backgroundColor: 'transparent',
            border: 'none',
            color: 'white',
            outline: 'none',
            cursor: 'pointer',
            font: 'inherit',
            padding: '10px',
            margin: '10px',
            fontWeight: 'bold',
        },
        Success: {
            color: '#5C9210',
        },
        Danger: {
            color: '#944317',
            cursor:'not-allowed'
        }
    }
    
    ButtonStyles.Button.color = props.type === 'Success' ? '#5C9210' : '#944317'
    ButtonStyles.Button.cursor = props.type === 'Success' ? 'pointer' : 'not-allowed'
    return(
        <button style={ButtonStyles.Button}
        onClick={props.clicked} data-testid={'btn-'+ props.children}>{props.children}</button>
    )
};

export default Button;